
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { ORDER_SUBMIT } from '@/api/orderSubmit'
export default {
name: 'OrderConfirm',
data () {
return {
orderTime: this.getNow(),
minutes: ['00', '15', '30', '45'],
list1: [[1, 2, 3, 4, 5]],
numbers: [1],
phone: this.getPhoneNumber(),
remark: ''
}
},
components: {
Datetime,
Group,
PopupPicker,
XInput
},
computed: {
foods () {
return JSON.parse(localStorage.getItem('carts'))
},
totalPrice () {
return this.foods.reduce((total, cur, index, arr) => {
return total + (cur.price * cur.number)
}, 0)
},
startDate () {
return dateFormat(new Date(), 'YYYY-MM-DD')
}
},
methods: {
getNow () {
return dateFormat(new Date(), 'YYYY-MM-DD HH:mm')
},
handleSubmit () {
const data = JSON.stringify({
telephone: this.phone,
count: this.numbers[0],
time: this.orderTime,
remark: this.remark,
goods: this.foods,
totalPrice: this.totalPrice
})
ORDER_SUBMIT(data).then(res => {
console.log(res)
if (res.code === 200) {
this.$emit('submitOrder')
this.$router.push('/pay/' + res.orderId)
}
})
},
getPhoneNumber () {
return JSON.parse(localStorage.getItem('userInfo')).telephone
},
handleHide () {
this.$emit('hideConfirm')
}
}
}



